@media (min-width: 768px) {
  .contenido-header {
    width: 85%;
    margin: 0 auto;
  }
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  width: 100%;
  top: 112%;
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
    position: relative;
    width: auto;
  }
}

.dark-mode-boton {
  font-size: 1.2rem;
  padding: 0.2rem;
  border-radius: 50%;
  top: 0.7rem;
  right: 0;
  position: absolute;
  cursor: pointer;
}
.dark-mode-boton.fa-moon {
  color: #FFFFFF;
  background-color: #070056;
}
.dark-mode-boton.fa-sun {
  color: #333333;
  background-color: #FFFFFF;
}
.dark-mode-boton::after {
  width: 0.2rem;
  height: 0.2rem;
  border-radius: 50%;
  background-color: #FFFFFF;
  content: "";
  display: block;
  position: absolute;
  top: 4px;
  right: 4px;
}
@media (min-width: 768px) {
  .dark-mode-boton {
    font-size: 1.6rem;
    top: 1.5rem;
    right: 0;
  }
}
@media (min-width: 2200px) {
  .dark-mode-boton {
    font-size: 2.6rem;
  }
  .dark-mode-boton::after {
    width: 0.5rem;
    height: 0.5rem;
    top: 6px;
    right: 6px;
  }
}

.light {
  filter: invert(65%);
}

.barra {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 1rem;
  align-items: center;
  position: relative;
  margin: 1rem auto;
  padding-bottom: 1rem;
  border-bottom: 0.1rem solid #070056;
}
.barra img {
  width: 7rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    border-bottom: none;
  }
  .barra img {
    width: 10rem;
  }
}
@media (min-width: 1024px) {
  .barra img {
    width: 12rem;
  }
}
@media (min-width: 2200px) {
  .barra {
    margin: 5rem 0;
  }
  .barra img {
    width: 20rem;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.2s linear;
  height: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    align-items: center;
    visibility: visible;
    opacity: 1;
    height: auto;
  }
}
.navegacion a {
  color: #070056;
  text-decoration: none;
  font-size: 1.4rem;
  display: block;
  text-align: center;
  background-color: rgba(83, 136, 177, 0.5);
  padding: 0.5rem 0;
  margin: 0.5rem;
  font-weight: 500;
}
.navegacion a:hover {
  background-color: #F2DB8F;
}
@media (min-width: 768px) {
  .navegacion a {
    background-color: transparent;
    font-size: 1.6rem;
    padding: 0;
    margin: 0;
    margin-right: 4.2rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
  .navegacion a:hover {
    color: #5388B1;
    background-color: transparent;
  }
}
@media (min-width: 1024px) {
  .navegacion a {
    margin-right: 6.5rem;
  }
}
@media (min-width: 1024px) {
  .navegacion a {
    font-size: 1.8rem;
  }
}
@media (min-width: 2200px) {
  .navegacion a {
    font-size: 3.2rem;
    margin-right: 15rem;
  }
}
.navegacion .submenu {
  color: #FFFFFF;
  text-decoration: none;
  font-size: 2.2rem;
  display: block;
  text-align: center;
  margin-left: 2.5rem;
  cursor: pointer;
}
.navegacion .dropdown {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.2s linear;
  height: 0;
  position: relative;
}
.navegacion .visible {
  position: absolute;
  visibility: visible;
  opacity: 1;
  height: auto;
  background-color: #383838;
}
.navegacion .visible a {
  text-align: center;
  padding: 0.2rem 2rem;
  margin: 0;
}
.navegacion .visible a:hover {
  background-color: #4a4a4a;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu .btnMenu {
  color: #070056;
  margin: 1rem 0;
  cursor: pointer;
}
.mobile-menu .btnMenu:hover {
  color: #5388B1;
}

.no-borde {
  border-bottom: none;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
  padding: 1rem;
  border-bottom: 0.1rem solid #070056;
  z-index: 11;
  background-color: #FFFFFF;
}

.active {
  color: #5388B1 !important;
}

.footer {
  background-color: #070056;
  padding: 1rem 0;
  position: relative;
}
.footer .contenedor h3, .footer .contenedor p, .footer .contenedor a, .footer .contenedor i {
  text-align: left;
  color: #FFFFFF;
}
.footer .contenedor p, .footer .contenedor a {
  font-size: 1.4rem;
  padding: 0;
  margin: 0;
}
@media (min-width: 2200px) {
  .footer .contenedor p, .footer .contenedor a {
    font-size: 2.2rem;
  }
}
.footer .creator p {
  font-size: 1.1rem;
  text-align: right;
  color: #f8f2b1;
  margin-right: 2rem;
  text-decoration: underline;
}
.footer .creator p span {
  color: #fda5c8;
  text-decoration: underline;
}
@media (min-width: 2200px) {
  .footer .creator p {
    font-size: 1.4rem;
  }
}
.footer .scroll-up {
  position: absolute;
  right: 5%;
  bottom: 15%;
}
.footer .scroll-up i {
  font-size: 3rem;
}

.footer-info {
  display: grid;
  place-items: center;
  border-bottom: 1px solid #FFFFFF;
}
@media (min-width: 768px) {
  .footer-info {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding-bottom: 2rem;
  }
}
.footer-info .info {
  display: block;
}
.footer-info .info .bloqueSedes {
  border-bottom: 1px solid #FFFFFF;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
.footer-info .info .bloqueSedes .sedes:nth-child(2) {
  grid-row: 3/4;
}
@media (min-width: 768px) {
  .footer-info .info .bloqueSedes {
    border-bottom: none;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .footer-info .info .bloqueSedes .sedes:nth-child(2) {
    grid-row: 1/2;
  }
  .footer-info .info .bloqueSedes .sedes:nth-child(3) {
    grid-column: 1/3;
  }
}
@media (min-width: 768px) {
  .footer-info .info a {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
}
.footer-info .info img {
  display: block;
  margin: auto;
  margin-top: 2.5rem;
  width: 10rem;
}
@media (min-width: 768px) {
  .footer-info .info img {
    margin: 0;
    margin-top: 2.5rem;
    width: 20rem;
  }
}
.footer-info .info .politicas {
  display: grid;
  place-items: center;
}
.footer-info .info .politicas p {
  text-align: center;
  margin: 1rem 0;
}
.footer-info .info .politicas p:hover {
  color: #5388B1;
}
@media (min-width: 768px) {
  .footer-info .info .politicas {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .footer-info .info .politicas p {
    text-align: right;
    margin: 1rem 0;
  }
  .footer-info .info .politicas p:hover {
    color: #5388B1;
  }
}

.info-contacto {
  text-align: center;
  padding: 1rem 0;
}
@media (min-width: 768px) {
  .info-contacto {
    text-align: left;
    display: flex;
    justify-content: space-between;
  }
}
.info-contacto .redes {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
  width: 50%;
  margin: auto;
  margin-top: 1rem;
  place-items: center;
}
.info-contacto .redes i {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .info-contacto .redes {
    width: 15%;
    margin-right: 7rem;
  }
  .info-contacto .redes i {
    font-size: 2.4rem;
    margin-right: 2.5rem;
  }
}
@media (min-width: 2200px) {
  .info-contacto .redes i {
    font-size: 3.5rem;
  }
}