@use "sass:math";
@use 'variables' as v;
@use 'mixins' as m;

.dark-mode{
    background-color: darken(v.$grisOscuro, 10);
    h1, h2, h2{
        color: lighten(v.$azulClaro,8);
    }

    .dark-mode-boton{
        &::after{
            height: 0.3rem;
            width: 0.3rem;
            top: 34%;
            right: 34%;
            @include m.giant{
                height: 0.6rem;
                width: 0.6rem;
            }
        }
    }

    .barra{
        border-bottom: 0.1rem solid v.$blanco;
        @include m.tablet{
            border-bottom: none;
        }
        img{
            filter: invert(100%) brightness(1.5);
        }
    }

    .navegacion{
        a{
            color: v.$blanco;
        }
    }

    .mobile-menu{
        .btnMenu{
            color: v.$blanco;    
            &:hover{
                color: v.$azulClaro;
            }
        }
    }
    .no-borde{
        border-bottom: none;
    }
    .mostrar{
        border-bottom: 0.1rem solid v.$blanco;
        background-color: darken(v.$grisOscuro, 9);
        a:hover{
            color: v.$grisOscuro;
        }
    }

    .footer{
        background-color: darken(v.$asignar, 9);
    }

    .enlaces-main{
        img, picture{
            filter: brightness(0.9);
        }
    }

    .valor{
        &::after{
            background-color: v.$azulClaro;
        }
        p{
            color: lighten(v.$azulClaro,8);
        }
    }
}