@use "sass:math";
@use 'variables' as v;
@use 'mixins' as m;

.seccion {
    margin-top: math.div(v.$separacion, 2);
    margin-bottom: math.div(v.$separacion, 2);
}

.seccion:last-of-type{
    margin-bottom: 0;
}

.contenido-centrado {
    max-width: 800px;
}

.alinear-derecha{
    display: flex;
    justify-content: flex-end;
}

.w-100{
    width: 100%;
}

.no-click{
    pointer-events: none;
}