@use "sass:math";
@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-header{
    @include m.tablet{
        width: 85%;
        margin: 0 auto;
    }
}
.derecha{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    width: 100%;
    top: 112%;

    @include m.tablet{
        align-items: flex-end;
        position: relative;
        width: auto;
    }
}
.dark-mode-boton{
    font-size: 1.2rem;
    padding: 0.2rem;
    border-radius: 50%;
    top: 0.7rem;
    right:0;
    position: absolute;
    cursor: pointer;

    &.fa-moon{
        color: v.$blanco;
        background-color: v.$asignar;
    }

    &.fa-sun{
        color: v.$grisOscuro;
        background-color: v.$blanco;
    }

    &::after{
        width: 0.2rem;
        height: 0.2rem;
        border-radius: 50%;
        background-color: v.$blanco;
        content: '';
        display: block;
        position: absolute;
        top: 4px;
        right: 4px;
        
    }

    @include m.tablet{
        font-size: 1.6rem;
        top: 1.5rem;
        right:0;
    }

    @include m.giant{
        font-size: 2.6rem;

        &::after{
            width: 0.5rem;
            height: 0.5rem;
            top: 6px;
            right: 6px;
        }
    }
}

.light{
    filter: invert(65%);
}

.barra{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding-top: 1rem;
    align-items: center;
    position: relative;
    margin: 1rem auto;
    padding-bottom: 1rem;    
    border-bottom: 0.1rem solid v.$asignar;

    img{
        width: 7rem;
    }

    @include m.tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        border-bottom: none;

        img{
            width: 10rem;
        }
    }
    @include m.desktop{
        img{
            width: 12rem;
        }
    }

    @include m.giant{
        margin: 5rem 0;
        img{
            width: 20rem;
        }
    }
}

.navegacion{
   visibility: hidden;
   opacity: 0;
   transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.2s linear;
   height: 0;
   width: 100%;

    @include m.tablet{
        display: flex;
        align-items: center;
        visibility: visible;
        opacity: 1;
        height: auto;

    }

    a{
        color: v.$asignar;
        text-decoration: none;
        font-size: 1.4rem;
        display: block;
        text-align: center;
        background-color: v.$azulFondo;
        padding: 0.5rem 0;
        margin: 0.5rem;
        font-weight: v.$medium;

        &:hover{
            background-color: v.$amarillo;
        }

        @include m.tablet{
            background-color: transparent;
            font-size: 1.6rem;
            padding: 0;
            margin: 0;
            margin-right: 4.2rem;

            &:last-of-type{
                margin-right: 0;
            }
            &:hover{
                color: v.$azulClaro;
                background-color: transparent;
            }
        }
        @include m.desktop{
            margin-right: 6.5rem;
        }
        @include m.desktop{
            font-size: 1.8rem;
        }
        @include m.giant{
            font-size: 3.2rem;
            margin-right: 15rem;
        }
    }

    .submenu{
        color: v.$blanco;
        text-decoration: none;
        font-size: 2.2rem;
        display: block;
        text-align: center;
        margin-left: 2.5rem;
        cursor: pointer;
    }

    .dropdown{
        visibility: hidden;
        opacity: 0;
        transition: visibility 0.3s ease-in-out, opacity 0.3s linear, height 0.2s linear;
        height: 0;
        position: relative;
    }
    .visible{
        position: absolute;
        visibility: visible;
        opacity: 1;
        height: auto;
        background-color: lighten(v.$grisOscuro, 2);
            a{
                text-align: center;
                padding: 0.2rem 2rem;
                margin: 0;

                &:hover{
                    background-color: lighten(v.$grisOscuro, 9);
                }
            }
    }

}

.mobile-menu{
    @include m.tablet{
        display: none;
    }

    .btnMenu{
        color: v.$asignar;
        margin: 1rem 0;
        cursor: pointer;

        &:hover{
            color: v.$azulClaro;
        }
    }
}

.no-borde{
    border-bottom: none;
}

.mostrar{
    visibility: visible;
    opacity: 1;
    height: auto;
    padding: 1rem;
    border-bottom: 0.1rem solid v.$asignar;
    z-index: 11;
    background-color: v.$blanco;
}

.active{
    color: v.$azulClaro !important;
}