@use "sass:math";
@use 'base/variables' as v;
@use 'base/mixins' as m;

.main-content{
    @include m.tablet{
        @include m.grid(3, 0);
    }

    .enlaces-main{
        position: relative;
        .enlaces{
            display: none;
            width: 100%;
            padding: 1rem 0;
            bottom: 12%;
            padding: 2rem;
            a{
                color: v.$blanco;
                border-radius: 2rem;
                padding: 0.5rem 1.5rem;
                border: 0.25rem solid v.$blanco;
                font-weight: v.$regular;
                font-size: 1.4rem;
                @include m.telefono{
                    padding: 1rem 2rem;
                    font-size: 1.6rem;
                    border: 0.3rem solid v.$blanco;
                }
                @include m.desktop{
                    padding: 1rem 3rem;
                    font-size: 1.6rem;
                    border: 0.3rem solid v.$blanco;
                }
                @include m.giant{
                    padding: 2rem 5rem;
                    font-size: 2.4rem;
                    border: 0.5rem solid v.$blanco;
                }
            }
        }

        .blend-main{
            background-color: v.$azulClaro;
            height: 42rem;

            @include m.desktop{
                height: auto;
            }

            img, picture{
                object-fit: cover;
                height: 100%;
            }
        }

        h1, .enlaces{
            position: absolute;
            text-align: center;
            padding: 0 2rem;
            width: 100%;
            z-index: 1;
        }

        h1{
            color: v.$asignar;
            padding: 0.5rem 2rem;
            text-shadow: 2px 1px 3px v.$blanco;
            @include m.tablet{
                font-size: 2.6rem;
            }
            @include m.desktop{
                font-size: 3.6rem;
            }
            @include m.bigScreen{
                font-size: 4.2rem;
            }
            @include m.giant{
                padding: 4rem 3rem;
                font-size: 5.6rem;
            }
        }
        
        &:hover .enlaces{
            display: flex;
            justify-content: space-around;
            align-items: center;
        }

        &:hover picture{
            mix-blend-mode: multiply;
        }

        &:hover img{
            mix-blend-mode: multiply;
        }
    }    
}

.valor{
    padding: 0 math.div(v.$separacion, 2) v.$separacion math.div(v.$separacion, 2);
    position: relative;
    @include m.tablet{
        padding: v.$separacion math.div(v.$separacion, 2);
    }
    @include m.desktop{
        padding: v.$separacion;
    }

    &::before{
        content: "";
        width: 75vw;
        height: 65rem;
        background-color: v.$azulFondo;
        display: block;
        position: absolute;
        right: 0;
        z-index: -1;
        @include m.telefono{
            width: 70vw;
            height: 65rem;
        }
        @include m.tablet{
            height: 50rem;
        }
        @include m.desktop{
            width: 75vw;
            height: 36rem;
        }
        @include m.bigScreen{
            height: 40rem;
        }
        @include m.giant{
            padding: v.$separacion;
                height: 52rem;
        } 
    }

    &::after{
        content: "";
        width: 70vw;
        height: 0.3rem;
        background-color: v.$asignar;
        display: block;
        position: absolute;
        top: 12.5%;
        left: 12%;
        @include m.telefono{
            top: 8%;
        }
        @include m.tablet{
            width: 75vw;
            top: 25%;
        }
        @include m.desktop{
            top: 32%;
        }
        @include m.giant{
            width: 80vw;
            top: 28%;
            left: 7%;
        }
    }

    h2{
        text-align: left;
        font-weight: v.$black;
        font-size: 2.8rem;
        width: 80%;
        @include m.telefono{
            width: 100%;
        }
        @include m.tablet{
            font-size: 4rem;
        } 
        @include m.giant{
            font-size: 5rem;
        } 
    }
    p{
        font-weight: v.$medium;
        text-align: center;
        color: v.$asignar;
    }
    .valores-section{
        width: 75vw;
        align-items: flex-start;
        display: grid;
        grid-template-columns: repeat(1,1fr);
        justify-content: flex-end;
        padding: 0;
        margin-left: 7%;
        justify-items: center;
        row-gap: 1rem;
        @include m.telefono{
            margin-left: 15%;
        }
        @include m.tablet{
            margin-left: 26%;
            width: 60vw;
            grid-template-columns: repeat(2,1fr);
            padding: math.div(v.$separacion, 2) 0rem;
        }
        @include m.desktop{
            margin-left: 13%;
            width: 75vw;
            grid-template-columns: repeat(4,1fr);
        }
        @include m.giant{
            margin-left: 17%;
        }   
    }
    .valores{
        display: grid;
        place-items: center;
        &:last-of-type{
            margin-right: 0;
        }
        & p{
            padding-top: 2rem;
            font-size: 1.8rem;
            width: 95%;
            margin: auto;
        }
        .icono{
            width: 5rem;
            height: 5.5rem;
        }
        @include m.telefono{
            margin-right:0;
            .icono{
                width: 7rem;
                height: 7rem;
            }
            & p{
                font-size: 1.8rem;
                line-height: 1.4;
            }
        }
        @include m.tablet{
            & p{
                font-size: 1.8rem;
            }
        }
        @include m.desktop{
            margin-right: 2rem;
        }

        @include m.bigScreen{
            margin-right: 2.5rem;
            .icono{
                width: 9rem;
                height: 9rem;
            }
            & p{
                font-size: 2rem;
                line-height: 1.6;
            }
        }
        @include m.giant{
            margin-right: 10rem;
            .icono{
                width: 18rem;
                height: 18rem;
            }
            & p{
                font-size: 3rem;
            }
        }        
    }
}

